.toggleMenu{position:fixed;left:0;z-index:100;background-color:#fff;height:100vh;width:300px;box-shadow:0px 0px 10px 0px #313131}.toggleMenu-main{width:300px;margin-top:20px;display:flex;flex-direction:column;align-items:center}.toggleMenu-main .toggleMenu-main-input{width:260px;padding:9px;margin-top:20px;margin-bottom:20px}.toggleMenu-main span{font-size:22px}.toggleMenu-main .toggleMenu-main-search{display:flex;flex-direction:column;align-items:center}.toggleMenu-main .toggleMenu-elements{display:flex;flex-direction:column;align-items:center;width:100%;height:calc(100vh - 150px);overflow:auto}.item{display:flex;justify-content:center;width:300px}.item img{height:40px}.item span{font-size:18px;margin-left:15px}.item:hover{cursor:pointer;background-color:rgba(0,0,0,0.05)}.item .item-block{width:200px;display:flex;align-items:center}

.header{position:fixed;z-index:100;width:100%;height:70px;display:flex;justify-content:center;border-bottom:1px solid black;background-color:#fff;box-shadow:0 0 5px 0 #575757}.header-panel{position:relative;width:100%;display:flex;justify-content:center;align-items:center}.header-panel .header-panel-nemuLeft,.header-panel .header-panel-nemuRight{position:absolute;z-index:100;width:50px;height:100%;font-size:40px;outline:none;background-color:rgba(0,0,0,0);border:none}.header-panel .header-panel-nemuLeft:hover,.header-panel .header-panel-nemuRight:hover{cursor:pointer;color:#474747}.header-panel .header-panel-nemuLeft{left:0;margin-left:80px}.header-panel .header-panel-nemuRight{right:0;margin-right:80px}.header-panel .header-panel-logo{display:flex;align-items:center;font-size:30px}.header-panel .header-panel-logo .logo{height:40px;margin-right:10px}@media (max-width: 575px){.logo-text{display:none !important}}.shadow{position:absolute;z-index:0;right:0;height:100vh;width:100vw;background-color:rgba(0,0,0,0.512)}.logo-link{color:black;text-decoration:none}.logo-link:hover{color:#474747}

.footer{width:100%;height:70px;display:flex;justify-content:center;background-color:#fff;margin-top:50px;border-top:1px solid black;box-shadow:0 0 5px 0 #575757}.footer-panel{width:70%;height:100%;display:flex;justify-content:space-around;align-items:center}.footer-panel div{width:300px;display:flex;flex-direction:column;align-items:center}.footer-panel .footer-panel-text{font-size:16px;display:flex;align-items:center}.footer-panel .footer-panel-text i{margin-right:7px}

.pokemonCard{margin:7px;border:1px solid #5e5d5d;border-radius:5px;background-color:#fff;box-shadow:0 0 5px 0 #575757;position:relative;height:200px;width:200px}.pokemonCard .pokemonCard-main{display:flex;align-items:center;flex-direction:column;height:100%}.pokemonCard .pokemonCard-main img{width:150px}.pokemonCard .pokemonCard-main span{font-size:20px}.pokemonCard:hover{box-shadow:0px 0px 5px 1px #3a3a3a}.pokemonCard:hover .pokemonCard-favoriteIcon{opacity:1;color:black}.link{text-decoration:none;color:black}.link:visited{color:black}.link:hover{color:#be2323}.pokemonCard-favoriteIcon{position:absolute;right:0;top:0;font-size:22px;margin:5px;opacity:0}.pokemonCard-favoriteIcon:hover{color:#be2323 !important;cursor:pointer}.favoriteIcon{color:#be2323 !important;opacity:1}

.mainPage{display:flex;justify-content:center;align-items:center;flex-direction:column;min-height:calc(100vh - 120px)}.mainPage-pokemons{margin-top:100px;width:80%;max-width:1200px;display:flex;justify-content:center;flex-wrap:wrap}.mainPage-morePokemons button{outline:none;background-color:rgba(0,0,0,0);border:none;background-color:#e7e7e7;color:#303030;border-radius:5px;padding:5px 20px;font-size:20px;margin-top:50px}.mainPage-morePokemons button:hover{background-color:#ddd;cursor:pointer}.mainPage-morePokemons button:focus{box-shadow:0 0 0 2px #e7e7e7}

.loader-container{width:100%;padding:30px 0;display:flex;justify-content:center}.lds-ring{display:inline-block;position:relative;width:80px;height:80px}.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #353535;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#353535 transparent transparent transparent}.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes lds-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.pokemonPage{display:flex;justify-content:center;align-items:center;min-height:calc(100vh - 270px);padding-top:150px}.pokemonPage-main{display:flex;flex-wrap:wrap;justify-content:center;margin-top:100px;width:70%;max-width:1100px;margin:7px;border:1px solid #5e5d5d;border-radius:5px;background-color:#fff;box-shadow:0 0 5px 0 #575757;padding:30px 0}.pokemonPage-main span{margin-left:12px;font-size:20px}.pokemonPage-main-pokemonImg,.pokemonPage-main-stats,.pokemonPage-main-spels{display:flex;flex-direction:column;padding:20px;width:300px;flex-grow:1;position:relative}.pokemonPage-main-pokemonImg{align-items:center}.pokemonPage-main-pokemonImg img{width:200px}.title{border-bottom:1px solid black;margin-bottom:22px;text-align:center;padding-bottom:25px;margin-right:12px}.types{margin-top:30px}.name{font-size:40px !important}.pokemonPage-main-pokemonImg-favoriteIcon{font-size:20px !important;margin-top:40px;cursor:pointer}

.favoritePage{display:flex;justify-content:center;align-items:center;flex-direction:column;min-height:calc(100vh - 120px)}.favoritePage-noFavoriteBlock{background-color:#e6e6e6;font-size:25px;padding:30px;border-radius:10px}.favoritePage-noFavoriteBlock span{display:flex;justify-content:center;align-items:center}.favoritePage-noFavoriteBlock i{font-size:80px;margin-left:30px;color:#818181}.favoritePage-pokemons{margin-top:100px;width:80%;max-width:1200px;display:flex;justify-content:center;flex-wrap:wrap}.favoritePage-pokemons .favoritePage-pokemons-title{font-size:30px;width:100%;text-align:center;border-bottom:1px solid black;padding-bottom:14px;margin-bottom:30px}

